<style scoped>
    a {
        color: #2d8cf0;
        font-size: 0.8rem;
        margin-bottom: 10px;
        display: block
    }

    .hint {
        color: #888888;
    }

    .item-div {
        margin-top: 20px;
        color: #333333;
    }

    .title {
        line-height: 50px;
    }
</style>
<template>
    <div>
        <Row style="text-align: left">
            <Col :span="18">
                <h2>成员简历 <span v-if="detail.status!==1" class="hint">(资料待审核)</span></h2>
                <Div style="margin: 30px">
                    <div>
                        <span style="font-size: 2rem;font-weight: bold">{{ detail.name }}</span>
                        <span class="hint" style="display: inline-block;margin-left: 10px">{{detail.position}}</span>
                    </div>
                    <span class="hint">{{ detail.signature }}</span>
                    <div class="item-div">
                        <h2 class="title">主要成就</h2>
                        <span>
                            {{detail.achievement}}
                        </span>
                    </div>
                    <div class="item-div">
                        <h2 class="title">个人履历</h2>
                        <span>
                            {{detail.resume}}
                        </span>
                    </div>
                </Div>
            </Col>
            <Col :span="6" style="text-align: right">
                <router-link v-if="detail.status===1" :to="{name:'memberEdit',query:{id:detail.id}}">编辑</router-link>
                <img v-if="detail.picture" :src="$url + detail.picture" width="100%"/>
            </Col>
        </Row>
    </div>
</template>

<script>
  import api from './api';

  export default {
    data() {
      return {
        detail: {}
      };
    },
    mounted() {
      this.loadData();
    },
    methods: {
      loadData() {
        api.detail({ id: this.$route.params.id }, res => {
          this.detail = res.data.data;
        });
      }
    }
  };
</script>
