import Api from '@/js/api/baseApi';

export default class {
  static index(data, callback, errorBack) {
    Api.request(this.uri() + 'index', callback, errorBack, data);
  }

  static save(data, callback, errorBack) {
    Api.request(this.uri() + 'save', callback, errorBack, data);
  }

  static detail(data, callback, errorBack) {
    Api.request(this.uri() + 'detail', callback, errorBack, data);
  }

  static delete(data, callback, errorBack) {
    Api.request(this.uri() + 'delete', callback, errorBack, data);
  }

  static uri() {
    return Api.url() + 'enter_team_member/';
  }
}
